@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    width: 90%;
    padding-top: 70px;
}

.movies_movie {
    box-shadow: 5px 5px 5px black;
    width: 45%;
    background-color: white;
    margin-bottom: 70px;
    padding: 20px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    border-radius: 5px;
}

.movies_movie img {
    position: relative;
    top: -40px;
    max-width: 120px;
    margin-right: 30px; 
    box-shadow: 5px 5px 5px #394254;
}

h2,
h4,
.movie_year {
    margin: 0;
    font-weight: 300;
}

h2 {
    margin-bottom: 5px;
    font-size: 24px;
}

.genres,
.movie_year,
.movie_summary {
    opacity: 0.5;
}

.genres {
    list-style: none;
    display: flex;
    padding:0;
    margin:0;
    margin-top: 5px;
}

.genres_genre {
    margin-right: 10px;
}
* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    height: 100%;
    background-color: #232d42;
    color: black;
    font-family: 'Open Sans', sans-serif;
}

html,
body,
#root,
#potato,
.container
 {
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader {
    width : 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center; 

}

.loader_text {
    color: white;

}


